
import { defineComponent } from "vue";

// component that looks like a button that either does something or goes somewhere
export default defineComponent({
  props: {
    // text to show
    text: String,
    // icon to show
    icon: String,
    // visual design
    // default: filled
    // "plain": no fill
    design: String,
    // location to link to
    to: String,
    // on click action
    click: Function,
  },
  computed: {
    component() {
      if (this.to) return "AppLink";
      else return "button";
    },
  },
});
